import React from 'react'
import { Field, IFieldApi } from 'react-form'
import styled from 'styled-components'

import { GMenuRecipe, WareCategoryTypeEnum } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { RecipeListTable } from '../molecules/RecipeListTable'
import { RecipeSuggestionPanel } from './RecipeSuggestionPanel'

interface IProps {
  categoryType: WareCategoryTypeEnum
}

export const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 96%;
  width: 100%;
`
export const FormFixedRow = styled.div`
  flex: 0 0 auto;
`
export const FormStretchedRow = styled.div`
  flex: 1 1 0;
  min-height: 0;
  position: relative;
`

export const CategoryRecipesView: React.FC<IProps> = ({ categoryType }) => {
  return (
    <Field field="recipes">
      {(fieldApi: IFieldApi<ReadonlyArray<GMenuRecipe.Fragment>>) => (
        <FormContainer>
          <FormFixedRow>
            <RecipeSuggestionPanel
              categoryType={categoryType}
              filterRecipes={RM.without(fieldApi.value)}
              onAdd={addedRecipe =>
                fieldApi.setValue(fieldApi.value.concat(addedRecipe))
              }
            />
          </FormFixedRow>
          <FormStretchedRow>
            <RecipeListTable
              recipes={fieldApi.value}
              isSortable={true}
              showRemoveColumn={true}
              showEditColumn={false}
              showVisibilityColumn={false}
              heightStretch={true}
              onRemove={recipeId =>
                fieldApi.setValue(
                  RM.reject(RM.propEq('id', recipeId), fieldApi.value),
                )
              }
              onSortRecipe={({ items }) => {
                fieldApi.setValue(items)
              }}
            />
          </FormStretchedRow>
        </FormContainer>
      )}
    </Field>
  )
}
